import {SET_CELL_SIZE, TOGGLE_GRID, TOGGLE_SCROLLBARS} from "@/store/mutations/mutation-types";

export default {
    [SET_CELL_SIZE](state, cellSize) {
        state.slideParams.cellSize = cellSize
    },
    [TOGGLE_GRID](state) {
        state.slideParams.showGrid = !state.slideParams.showGrid
    },
    [TOGGLE_SCROLLBARS](state) {
        state.slideParams.isEntirelyVisible = !state.slideParams.isEntirelyVisible
    },
}