<template>
  <div
      class="content_wrapper"
      :class="{entirely_visible: isEntirelyVisible}"
      ref="content_wrapper"
  >
    <div
        class="content"
        ref="content"
        :style="contentStyles"
    >
      <slot v-if="ready"></slot>
    </div>
  </div>
</template>

<script>
import {mapState} from "vuex";

export default {
  name: 'RatioKeeper',
  props: {
    ratio: Number,
    minHeight: Number,
  },
  data() {
    return {
      ready: false,
      height: 0,
      baseHeight: 0,

      paddingX: 20,
      paddingY: 16,
      // horizontalScrollPadding: 0,
      horizontalScrollPadding: 20,
    }
  },
  computed: {
    ...mapState({
      isEntirelyVisible: state => state.slideParams.isEntirelyVisible,
    }),
    contentWidth() {
      return this.contentHeight * this.ratio + this.paddingX * 2
    },
    contentHeight() {
      return this.height - this.paddingY * 2
    },
    baseContentHeight() {
      return this.baseHeight - this.paddingY * 2
    },
    contentStyles() {
      return {
        width: this.contentWidth + 'px',
        height: this.height + 'px',
        padding: `${this.paddingY}px ${this.paddingX}px`,
        'font-size': 16 * this.contentHeight / this.baseContentHeight + 'px',
      }
    },
  },
  watch: {
    ratio() {
      this.keepAspectRatio()
    },
    isEntirelyVisible() {
      this.changeSelf(this.keepAspectRatio)
    },
  },
  methods: {
    changeSelf(transformation) {
      this.ready = false
      transformation().then(() => this.ready = true)
    },

    getBaseHeight() {
      let newHeight = this.$refs.content.parentElement.clientHeight - this.horizontalScrollPadding
      if (newHeight > this.minHeight) {
        return newHeight
      } else {
        return this.minHeight
      }
    },

    async keepAspectRatio() {
      this.baseHeight = this.getBaseHeight()

      if (this.isEntirelyVisible) {
        let wrapperWidth = this.$refs.content_wrapper.clientWidth
        let wrapperHeight = this.$refs.content_wrapper.clientHeight

        if (wrapperWidth / wrapperHeight > this.ratio) {
          // нужно добавлять поля по бокам
          this.height = wrapperHeight
        } else {
          // нужно добавлять поля сверху
          this.height = wrapperWidth / this.ratio
        }
      } else {
        this.height = this.baseHeight
      }
    },
  },
  created() {
    window.addEventListener('resize', () => this.changeSelf(this.keepAspectRatio))
  },
  mounted() {
    this.changeSelf(this.keepAspectRatio)
  },
  destroyed() {
    window.removeEventListener('resize', () => this.changeSelf(this.keepAspectRatio))
  },
}
</script>

<style scoped lang="scss">
.content_wrapper {
  width: 100%;
  height: 100%;

  &.entirely_visible {
    display: flex;
    justify-content: center;
    align-items: center;
  }
}

.content {
  margin: 0 auto;
}
</style>
