<template>
  <SlideEditor id="slide_editor">
        <template #params>
          <div class="col-4 col-md-3 col-lg-2">
            <label
                for="slidePriceInput"
                class="form-label"
            >
              Кол-во баллов
            </label>
            <input
                type="number"
                class="form-control"
                id="slidePriceInput"
                placeholder="Введите число"
                :value="activeSlide.price"
                @input="setSlidePrice($event.target.value)"
                min="0"
            >
          </div>
        </template>

    <!--    <template #controls>-->
    <!--      SingleChoice controls-->

    <!--      <button-->
    <!--          @click="addText"-->
    <!--      >-->
    <!--        Text (max 2)-->
    <!--      </button>-->
    <!--    </template>-->
  </SlideEditor>
</template>

<script>
import SlideEditor from "@/components/SlideEditor";
import {ADD_TEXT_ELEMENT} from "@/store/actions/action-types";
import Restricted from "@/components/mixins/Restricted";
import {mapGetters, mapMutations} from "vuex";
import {SET_SLIDE_PRICE} from "@/store/mutations/mutation-types";

export default {
  name: 'SingleChoice',
  components: {
    SlideEditor,
  },
  mixins: [Restricted],
  props: {},
  data() {
    return {
      restrictions: {
        // TextObject: {min: 1, max: 2},
      }
    }
  },
  computed: {
    ...mapGetters(['activeSlide'])
  },
  methods: {
    ...mapMutations({
      setSlidePrice: SET_SLIDE_PRICE,
    }),
    addText() {
      this.tryAction('TextObject', ADD_TEXT_ELEMENT, {x: 5, y: 5, width: 20, height: 10})
    },
  },
}
</script>

<style lang="scss" scoped>
</style>
