<template>
  <div
      class="slide"
      ref="slide"
  >
    <div
        v-if="isShowingGrid"
        class="grid"
        :style="{'background-size': backgroundSize}"
    ></div>

    <SlideElement
        v-for="slideElement in activeSlideElements"
        :key="slideElement.id"
        v-bind="{element: slideElement, slideWidth, slideHeight, cellSize, isInEditMode}"
    />
  </div>
</template>

<script>
import SlideElement from "@/components/SlideElement";
import {mapState, mapGetters} from "vuex";
import {SET_CELL_SIZE} from "@/store/mutations/mutation-types";

export default {
  name: 'Slide',
  components: {SlideElement},
  props: {
    isInEditMode: {
      type: Boolean,
      default: false,
    }
  },
  data() {
    return {
      backgroundSize: 'calc(100% / 16) calc(100% / 9)',
      multiplier: 4,
    }
  },
  computed: {
    ...mapState({
      slideWidth: state => state.slideParams.width,
      slideHeight: state => state.slideParams.height,
      cellSize: state => state.slideParams.cellSize,
      showGrid: state => state.slideParams.showGrid,
    }),
    ...mapGetters([
      'activeSlideElements'
    ]),
    isShowingGrid() {
      return this.showGrid && this.isInEditMode
    },
  },
  watch: {},
  methods: {
    setGridSize() {
      this.$store.commit(SET_CELL_SIZE, this.$refs.slide.clientWidth / this.slideWidth)
      this.backgroundSize = `${100 / this.slideWidth}% ${100 / this.slideHeight}%`
    },
  },
  created() {
  },
  mounted() {
    this.setGridSize()
  },
}
</script>

<style scoped lang="scss">
$gridColor: rgb(234, 234, 234);
$gridWidth: 1px;

.slide {
  position: relative;
  width: 100%;
  height: 100%;
  background-color: white;

  .grid {
    position: absolute;
    width: 100%;
    height: 100%;
    background-image: linear-gradient(to right, $gridColor $gridWidth, transparent 1px),
    linear-gradient(to bottom, $gridColor $gridWidth, transparent $gridWidth);
    border-bottom: $gridColor $gridWidth solid;
    border-right: $gridColor $gridWidth solid;
  }
}
</style>
