<template>
  <div class="slide_editor">
    <header>
      <Heading/>
    </header>

    <main>
      <Sidebar id="sidebar"/>

      <keep-alive v-if="isAnySlideSelected">
        <component :is="activeSlideComponent">
        </component>
      </keep-alive>

      <!--      <component-->
      <!--          v-if="isAnySlideSelected"-->
      <!--          :is="activeSlideComponent"-->
      <!--      >-->
      <!--      </component>-->

      <div
          v-else-if="slideIds.length > 0"
          class="help_text"
      >Выберите слайд слева
      </div>

      <div
          v-else
          class="help_text"
      >Чтобы начать редактирование, добавьте слайд
      </div>
    </main>
  </div>
</template>

<script>
import Heading from '@/components/Heading.vue';
import Sidebar from "@/components/Sidebar";
import {mapGetters} from "vuex";
import TypeNotSelected from "@/components/slide-types/TypeNotSelected";
import SingleChoice from "@/components/slide-types/SingleChoice";
import Info from "@/components/slide-types/Info";
import Voting from "@/components/slide-types/Voting";

export default {
  name: 'Editor',
  components: {
    Heading,
    Sidebar,

    TypeNotSelected,
    SingleChoice,
    Voting,
    Info,
  },
  computed: {
    isAnySlideSelected() {
      return this.activeSlide !== undefined
    },
    ...mapGetters([
      'slideIds',
      'activeSlide',
    ]),
    activeSlideComponent() {
      return this.$store.getters.activeSlideComponent || 'TypeNotSelected'
    },
  },
}
</script>

<style scoped lang="scss">
.slide_editor {
  height: 100%;
  display: flex;
  flex-direction: column;

  header {
  }

  main {
    flex: 1 1 auto;
    overflow: hidden;
    display: flex;

    #sidebar {
      min-width: min(20%, 290px);
      width: min(20%, 290px);
    }

    #slide_editor {
      flex: 1 1 auto;
    }

    .help_text {
      flex: 1 1 auto;
      display: flex;
      justify-content: center;
      align-items: center;

      padding: 0 5% 5%;

      font-size: 2rem;
      color: #8c8c8c;
    }
  }
}
</style>
